import React from 'react';
import { FormControl, InputLabel, Select, MenuItem } from '@mui/material';

export default function FilterSelect({ mode, onChange, sx }) {
  return (
    <FormControl variant="outlined" size="small" sx={sx}>
      <InputLabel id="filter-label"></InputLabel>
      <Select
        labelId="filter-label"
        value={mode}
        label="View"
        onChange={e => onChange(e.target.value)}
      >
        <MenuItem value="recommend">  </MenuItem>
        <MenuItem value="custom">  </MenuItem>
        <MenuItem value="progress">  </MenuItem>
        <MenuItem value="furthest">  </MenuItem>
        <MenuItem value="closed"> </MenuItem>
      </Select>
    </FormControl>
  );
}